@use '../../reference/' as reference;

.modal {
    width: 100%;
    height: 100%;
}

.overlay {
    position: fixed;
    overflow-y: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    background-color: reference.$darker-grey-60;
}

.close {
    font-size: 22px;
    color: reference.$darker-grey;
    cursor: pointer;
    user-select: none;
    padding: 0px 24px 24px 0px;
    transition: color reference.$transition-long;

    @media (max-width: 768px) {
        padding-right: 12px;
    }

    &Container {
        display: flex;
        justify-content: right;
        align-items: right;
    }
}

.close:hover {
    color: reference.$medium-grey;
    transition: color reference.$transition-long;
}

.close:active {
    transform: scale(0.99);
    transition: transform reference.$transition-short;
}
